body{font-family:'Open Sans',sans-serif;font-size:18px;color:#010000;font-weight:400;font-style:normal}a{text-decoration:none;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}a:hover{color:#ff1755;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out}.container-fluid{line-height:24px}header{position:relative;display:block;width:100%;background:#010000;color:#fff;height:90px}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}header .logo a{text-decoration:none;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer;color:#fff;font-size:30px}header .logo a:hover{color:#ff1755;-webkit-transition:.4s ease-in-out;-o-transition:.4s ease-in-out;transition:.4s ease-in-out}header nav,header nav ul,header nav ul li{margin:0;padding:0}header nav{position:absolute;top:50%;right:15px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:10px}header nav ul li.last{margin:0}header nav ul li a{font-size:19px;line-height:normal;color:#fff}


