#modalButton{
  background: rgba(55, 85, 94, 0.3);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 20px;
  width: 100%;
  border: none;
  height: 3em;
  margin-right: auto;
  margin-left: auto;
  padding-left: 2.1em;
  padding-right: 2.1em;
}

#modalButton:hover{
  background: #66ff66;
  color: white;
}

#modalBox{
  display: block;
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation-name: modalOpen;
          animation-name: modalOpen;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

#modalContent{
  background: white;
  max-width: 630px;
  margin: 5% auto;
  padding: 20px;
  width: 90%;
  -webkit-box-shadow: 0 0 20px 0 rgba(72,94,116,0.6);
          box-shadow: 0 0 20px 0 rgba(72,94,116,0.6);
  border-radius: 5px;
  /*box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0, 0.2),
              0 7px 20px 0 rgba(0, 0, 0, 0, 0.15);*/
}

#closeButton{
  color: #ccc;
  float: right;
  font-size: 30px;
}

#closeButton:hover, #closeButton:focus{
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

@-webkit-keyframes modalOpen {
  from{opacity: 0 }
  to{opacity: 1}
}

@keyframes modalOpen {
  from{opacity: 0 }
  to{opacity: 1}
}

#modalButtonEdit{
  float: right;
  background: white;
  border: 2px solid #4d4d4d;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  color: #4d4d4d;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

  font-size: 18px;
  margin-right: 3px;
}

#modalButtonEdit:hover{
  background: #4dff4d;
  color: white;
  border: 2px solid white;
}


